.password{
display: flex;
flex-direction: column;
order: 1;
}

.message{
  display: flex;
  flex-direction: column;
  order: 3;
}

.emailAddress{
  display: flex;
  flex-direction: column;
  order: 2;
}

.contactBtn{
text-align: right;
margin-top: 2rem;
margin-bottom: 2rem;
display: flex;
flex-direction: column;
order: 4;
}

.emailAndPass{
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-evenly;
  flex-grow: 1;
  padding: 2rem;

}

.textbox{
  flex-direction: row;
justify-content: space-evenly;
  flex-grow: 2;
  padding-left: 2rem;
  padding-right: 2rem;
  /* margin-top: 2rem; */
  
}

label{
  text-align: left;
}


